
import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';
import { PackageService } from './package.service';
import { Package } from './package.model';

@Component({
  selector: 'app-package-detail-container',
  template: `
    <div class="home-container">
      <ngx-loading [show]="loading" [config]="{ backdropBorderRadius: '3px' }"></ngx-loading>
      <mdc-card class="demo-card" style="margin-bottom: 40px;" outlined>
        <mdc-ripple class="demo-card-article">
          <app-package-header [pkg]="pkg$ | async"></app-package-header>
        </mdc-ripple>
      </mdc-card>
      <div class="markdown-body" [innerHtml]="readme$ | async"></div>
    </div>
  `
})

export class PackageDetailContainerComponent implements OnInit {

  loading = false;

  pkg$ = new BehaviorSubject<Package>(null);
  readme$ = new BehaviorSubject('');

  constructor(
    private service: PackageService,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.loading = true;
    this.route.params.pipe(
      switchMap(params => this.service.findByName(params.name)),
      tap(pkg => this.pkg$.next(pkg)),
      switchMap(pkg => this.service.fetchReadme(pkg)),
      tap(readme => this.readme$.next(readme)),
    ).subscribe(() => this.loading = false);
  }
}
